.userPhoto {
    display: inline-block;
    width: 50px;
    height: 50px;
    border-radius: 50px;
}

.selectedPage {
    font-weight: bold;
    margin: 3px;
}

.usersWeb {
    display: grid;
    grid-template-columns: 520px 220px;
}

.userTileInfo2 {
    display: grid;
}

.userTileInfo1 {
    display: grid;
}

.userTileInfo2:hover {
    background-color: white;
}

.userTileInfo1:hover {
    background-color: forestgreen;
}

.usersWeb:hover {
    background-color: #8caaee;
}